        "description": "Pronounced brock-do-bor-bon-NAY, this pointer has a calm, gentle demeanor that is ideally suited for the foot hunter. The breed's relatively small size and short coat well suits them as an ideal home companion for the family that hunts.The Bourbonnais is an ancient breed that evolved in France during the 15th century in the historic province of Bourbonnais. By the 1800s, the breed was known amongst French hunters as a tailless pointer with distinctive fawn and liver ticking. The Braque du Bourbonnais has now gained a strong foothold in North America; some years there are more pups registered in the United States than its homeland of France. In English, he is the Bourbonnais Pointing Dog. He is kind and affectionate in the home and a serious, adaptable, intelligent hunter in the field.           Club Contact Details     Club: Braque du Bourbonnais Club of America    Cindy Petkwitz    villagelock58@comcast.net    572 Hollywood, Grosse Pointe, MI 48236    (313) 881-8603Pronunciation - Brock do - Boor-bon-nay",
