        "name": "Braque Saint-Germain",
        "description": "The Braque St. Germain is thought to originate in France from crossing a Braque Francais and a Pointer in the 1800's. The Braque St. Germain was originally called Compiegne Pointers due to the location of the kennel that they were developed at. However, the name was changed when the kennel was relocated to the Saint Germain region.The Braque St. Germain is thought to originate in France from crossing a Braque Francais and a Pointer in the 1800's. The Braque St. Germain was originally called Compiegne Pointers due to the location of the kennel that they were developed at. However, the name was changed when the kennel was relocated to the Saint Germain region.Contact: Cindy Email: cp.villagelockgp@gmail.com",
