        "name": "Coton de Tulear",
        "description": "The Coton de Tulear, 'Royal Dog of Madagascar,'Â is a bright, happy-go-lucky companion dog whose favorite activities include clowning, cavorting, and following their special human around the house. The Coton is small but robustly sturdy.The Coton de Tulear is a small, immensely charming dog standing between 9 and 11 inches high and weighing anywhere from 8 to 13 pounds. Cotons are known for a profuse white coat that is as soft as cotton (or, as the French say, 'coton'). Their primary job is to provide amusement, comfort, and companionship.     The bond between Cotons and their people is so tight that owners discuss it in human terms. Coton fanciers describe them as 'witty' companions 'at times boisterous but never demanding'Â and 'naturally clownish and lighthearted,' who possess a 'remarkably gentle, sympathetic awareness.' Amusing traits of these long-lived jesters include unique vocalizations and a knack for walking on their hind legs.Pronunciation - Ko-Ton de Two-lay-air",
