        "name": "Deutscher Wachtelhund",
        "description": "The Deutscher Wachtelhund has a scenting and blood tracking ability comparable to a Bloodhound and can track 40-hour-old wounded game. On a fresh game trail, it must vocalize to inform hunters of its location. They are a versatile breed, virtually unknown to the German non-hunting public, and are only owned by hunters, gamekeepers, and professional hunters in Germany.The Deutscher Wachtelhund is a medium-sized, long-haired, very muscular gun dog with a noble head and strong bone. Their keen hunting desire and ability make them an ideal dog for hunters and because of their innate love of water, they are often used to hunt waterfowl. The Deutscher Wachtelhund's name, German quail dog, relates to its ability as an upland bird flusher and is also known as the German Spaniel.            Club Contact Details:   Bill WrightBoulder Brook Kennel, PO Box 1, Markham, VA 22643    jwwiii@hotmail.comPronunciation - Doy-cher Walk-tel-hund",
