        "name": "English Cocker Spaniel",
        "description": "English Cocker Spaniel lovers often use the word 'merry'Â to describe their breed. Upbeat in the field and mellow at home, this compact, silky-coated bird dog is widely admired for his delightful personality and irresistible good looks.The English Cocker Spaniel is a compactly built sporting dog standing between 15 to 17 inches at the shoulder. The softly contoured head, with its dark, melting eyes that convey an alert and dignified expression, is framed by lush, close-lying ears. The medium-length coat, seen in a variety of striking colors and patterns, is silky to the touch. 'Balance' is a key word in understanding the breed: The EC is balanced in temperament, construction, and movement. Beneath the EC's physical beauty beats the heart of a tireless, eager-to-please hunter's helper, famous the world over for his ability to flush and retrieve gamebirds. For those who prefer more domestic pursuits, there is no more charming and agreeable household companion.",
