        "name": "German Pinscher",
        "description": "The sleek, no-frills German Pinscher is among Germany's oldest breeds and the prototype of other pinscher breeds. This energetic, super-intelligent dog was at first used as a rat catcher but can be trained for all types of canine work.Sleek, shiny, and streamlined, GPs are a true dog lover's delight. They're eye-catching and elegant but in all ways honestly doggy's nothing fussy or exaggerated. They'll stand about knee-high to the average adult. Muscles ripple beneath a shimmering coat of red, black or blue with red accents. There's nobility in the elongated, wedge-shaped head, and the dark eyes are alert and eager. GPs move with the kind of strong, free-and-easy gait that tells you this is a confident companion, up for anything the day might bring.",
