        "name": "Glen of Imaal Terrier",
        "description": "Gentler, less excitable than most terriers, but still bold and spirited, the double-coated Glen of Imaal Terrier is named for one of Ireland's most remote locales. The brave but docile Glen is a strong, no-fuss dog built for hard work.Glens are scruffy, sturdy, low-slung terriers standing no more than 14 inches at the shoulder. There's nothing fancy or fussed-over about Glens. Rather, their wiry no-frills coat, broad head, and bowed front legs suggest a working farm dog from a time and place where substance was more important than style. And yet, they're also ridiculously cute. It takes a heart of stone to resist reaching down to give a Glen a scratch behind the ear and a pat on the well-muscled rump.",
