        "description": "The Japanese Chin is a charming toy companion of silky, profuse coat and an unmistakably aristocratic bearing. Often described as a distinctly 'feline'Â breed, this bright and amusing lapdog is fastidious, graceful, and generally quiet.Chin are the unrivaled noblemen of Japanese breeds. They're tiny 'indoorsy'Â companions, with an unmistakably Eastern look and bearing. The head is large, the muzzle short, and the round, dark eyes convey, as Chin fans like to say, a 'look of astonishment.'Â The profuse mane around the neck and shoulders, the plumed tail arching over the back, and the pants or 'culottes'Â on the hind legs project the elegant, exotic appearance so typical of Asia's royal line of laptop cuddle bugs.",
