        "name": "Komondor",
        "description": "A powerfully large Hungarian flock guardian covered in profuse white cords from head to tail, the Komondor is among the world's most recognizable breeds. The independent and protective Kom requires a firm, experienced hand at training.Our Hungarian-language tip of the day: The plural of Komondor is Komondorok. Now, let's take a look at the dog. What we see is a large and powerful guardian covered in profuse white cords. A big male can stand more than 27.5 inches and weigh over 100 pounds, with heavy bone, a deep chest, and lots of muscle. Despite their brawn Komondorok are agile and light-footed, and move with long strides. With their unique look, it's impossible to mistake Koms for anything else.",
