        "name": "Lagotto Romagnolo",
        "description": "The Lagotto Romagnolo, Italy's adorable 'truffle dog,' sports a curly coat and lavish facial furnishings. Despite their plush-toy looks, Lagotti are durable workers of excellent nose who root out truffles, a dainty and pricey delicacy.Italians have a word for it: 'carino.' In English, we say 'cute.' In any language, this breed is totally endearing. The Lagotto Romagnolo (plural: Lagotti Romagnoli) is known for wooly curls that cover the body head to tail, crowned by a lavish beard, eyebrows, and whiskers. Lagotti stand under 20 inches and weigh no more than 35 pounds. But don't be fooled by their teddy-bear looks, these are rugged workers of true strength and endurance. The breed's trademark curls feel and behave more like human hair than fur.Pronunciation - Low-got-tow Ro-man-yow-low",
