        "name": "Lakeland Terrier",
        "description": "A bold, zesty \"big dog in a small package,\" the Lakeland Terrier, named for the Lake District of his native England, was once a farmer's dog bred to work in packs on sheep-stealing foxes. The Lakie's coat is hard, wiry, and low-shedding.Square and sturdy Lakelands, standing less than 15 inches at the shoulder and weighing about 17 pounds, are small dogs. But don't tell them that. With their cock-of-the-walk swagger, Lakelands personify the old dog-lover's clichÃ© \"a big dog in a small package.\" They come in several colors; some have a sporty saddle mark on the back. With folded V-shaped ears, a straight front, rectangular head, and a mischievous twinkle in their eye, Lakelands are the blueprint of a long-legged British terrier.",
