        "name": "Miniature Bull Terrier",
        "description": "In most every way the Miniature Bull Terrier is a Bull Terrier, only smaller. These upbeat, mischievous dogs come equipped with terrier fire and fearlessness. If ever a dog could claim the title 'Clown Prince of Dogdom,' it's the Mini.Like the class clown or an adorable toddler, the Miniature Bull Terrier is full of mischief and high spirits. With square bodies and egg-shaped heads, no other breed looks quite like it. These small, muscular terriers are strong, active, fearless, and endlessly entertaining. As fun as he is, it's not a good idea to let your Miniature Bull Terrier puppy get away with too many high jinxes. He'll be at his absolute best with early socialization and patient training; you couldn't ask for a more amusing companion.",
