        "name": "Pekingese",
        "description": "The Pekingese, a compact toy companion of regal bearing and a distinctive rolling gait, is one of several breeds created for the ruling classes of ancient China. These are sophisticated dogs of undying loyalty and many subtle delights.Pekingese are compact, stocky toy dogs weighing up to 14 pounds. The coat is longest at the neck and shoulders, giving Pekes their famous 'lion's mane.'Â Coats come in various reds, from a golden-red to darker shades. The large, short-muzzled head is a wider-than-long 'envelope shaped'Â rectangle, and the eyes are large, dark, and sparkly. A unique feature of Pekes is their effortless 'rolling'Â gait. Pekes are charming, confident companions who develop a tight bond with their favorite human. Bred to live in palaces, they can be as serenely independent as the emperors who owned them. (They're 'opinionated,'Â Peke people say.) Ever alert, they make good watchdogs. Pekes will tolerate kids but won't stand for a lot of roughhousing.",
