        "name": "Pug",
        "description": "Once the mischievous companion of Chinese emperors, and later the mascot of Holland's royal House of Orange, the small but solid Pug is today adored by his millions of fans around the world. Pugs live to love and to be loved in return.The Pug's motto is the Latin phrase 'multum in parvo'Â (a lot in a little)'Â¿an apt description of this small but muscular breed. They come in three colors: silver or apricot-fawn with a black face mask, or all black. The large round head, the big, sparkling eyes, and the wrinkled brow give Pugs a range of human-like expressions'Â¿surprise, happiness, curiosity'Â¿that have delighted owners for centuries. Pug owners say their breed is the ideal house dog. Pugs are happy in the city or country, with kids or old folks, as an only pet or in a pack. They enjoy their food, and care must be taken to keep them trim. They do best in moderate climates'Â¿not too hot, not too cold'Â¿but, with proper care, Pugs can be their adorable selves anywhere.",
