        "description": "The Romanian Mioritic Shepherd Dog origins come from near the Carpathian Mountains. The breed is very impressive in size and character.Used for centuries to guard livestock in its homeland of Romania, the Mioritic Sheepdog is a typical Livestock Guarding Dog (LGD). Loyal, reliable, independent, and fiercely protective, he is friendly and devoted to his family but very mistrusting of strangers. It is a large dog with long coat which requires regular grooming, and with any dog of this size and temperament requires daily exercise and basic obedience training. Mioritics are lively and stubborn but in spite of all their independence they are very sensitive creatures in their furry armor. As puppies they stick to the pack which has accepted them. This can be any kind of creature ' dog, cat, horse, sheep, the Mioritic needs very close family ties; a house without doors it thinks would be ideal. This certainly depends a great deal on the weather. Its favorite place is always very close to its favorite person. It determines its resting place itself. This may even be the couch or a carpet not suitable for dogs, but as a rule shepherds, even as puppies, prefer to sleep on hard ground. They need no dog baskets, no special mattresses and by no means an allocated place. No 'work creating' program is necessary but daily walks and hikes provide exciting variation.Pronunciation - Romanian Me-or-it-ick Shepherd Dog",
