        "description": "The Segugio Italiano is a loyal and personable breed of great stamina, great heart, and impeccable intelligence.Fleet of foot and possessed of elegance, the Segugio Italiano is a tenacious hunter with superlative stamina. Its willing nature, intelligence, gentle and docile demeanor make it ideal as a companion, and well suited for people of all ages. It works diligently as an eager pet to conform to the needs of its owner.  Such character, coupled with its unwavering loyalty, has made it the darling of Italy.  Well-mannered and vigilant, the Segugio Italiano makes an excellent watch dog, watching both property and owner. You will find it neither aggressive nor shy, but courageous, with a cautious nature.  The Segugio Italiano is not a bold breed. It is adapt to any living situation provided it has enough mental and physical exercise. The dog comes in two coat types, the Pelo Raso (short-hair) and Pelo Forte (wire-hair). Within those coat types you will find the Segit to be either fawn or black & tan with very little white visible on the coat. Both coat varieties shed, but minimally.Club: Segugio Italiano Club of America - www.segugioitalianoclubofamerica.comEmail: secretary@segit.orgPronunciation - Say-gu-joe Italiano",
