        "description": "The heavenly breed with the heart of a lion,'Â the long, low, and level Skye Terrier is among the AKC's most distinctive-looking breeds. This elegant but sturdy aristocrat was bred as an exterminator on Scotland's remote Isle of Skye.Long, low, and level, this unique earthdog is among the AKC's most distinctive-looking breeds. Skyes stand 9 or 10 inches high and feature a long, flat-lying coat and peekaboo hairdo. They're known for big, feathery ears that stand up like bat wings, but Skyes can also have 'drop ears,'Â which lie flat against the large, long head. Beneath the profuse coat are short, muscular legs and a deep chest. When seen in profile, these elegant but substantial terriers are twice as long as they are high.",
