        "description": "This spirited, persistent hunter will follow a scent for hours. The Slovensky Kopov is a hardy, hardworking breed that is also known for its courage and great endurance and was originally developed to hunt wild boar.Also known as the Slovakian Hound, Slovak Hound, or the Slovensky Kopov is a medium-sized scent hound with a relatively light, but solid, build. His coat is of medium coarseness, close-fitting, dense, and always solid black with tan markings. It is an extremely intelligent breed with an excellent sense of smell and direction. Where he is known, he dominates the world of hunting by his endurance, character, and great courage.Club Contact Details Contact: Vladimir Skala Address: 3734 N Lockwood Ave, Chicago, IL 60641Pronunciation - Slovensky Cop-of",
